import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
export const Example = () => (
	<Section
		title='Examples'
		context='here is a quick example of the form style.'>
		<HoverCopy code={code.Example}>
			<CodeReadOnly code={code.Example} />
		</HoverCopy>
		<div className="n-form-field">
			<label>Username</label>
			<input className="n-form-control" type="text" placeholder="enter username..." name="username"/>
		</div>
		<div className="n-form-field">
			<label>Password</label>
			<input className="n-form-control" type="password" placeholder="enter password..." name="password"/>
		</div> 
	</Section>
);

export const Validation = () => (
	<Section
		title='Validation Design'
		context='it use to show visibility of validation.'>
		<HoverCopy code={code.Validation}>
			<CodeReadOnly code={code.Validation} />
		</HoverCopy>
		<div className="n-form-field n-form-valid">
			<label>Username</label>
			<input className="n-form-control" type="text" defaultValue="mytabworks" name="username"/>
			<div className="n-valid-feedback"><span role="img" aria-label="checked">✔️</span> username is valid!</div>
		</div>
		<div className="n-form-field n-form-invalid">
			<label>Password</label>
			<input className="n-form-control" type="password" defaultValue="123456" name="password"/>
			<div className="n-invalid-feedback">wrong credentials</div>
		</div> 
	</Section>
);

export const Checkbox = () => (
	<Section
		title='Checkbox'
		context='it a user select ZERO or MORE options of a limited number of choices.'>
		<HoverCopy code={code.Checkbox}>
			<CodeReadOnly code={code.Checkbox} />
		</HoverCopy>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control" type="checkbox" name="cb-ex" value="1" />checkbox 1</label>
			<label className="n-form-check"><input className="n-form-control" type="checkbox" name="cb-ex" value="2" />checkbox 2</label>
			<label className="n-form-check"><input className="n-form-control" type="checkbox" name="cb-ex" value="3"/>checkbox 3</label>
		</div> 
	</Section>
);

export const Radio = () => (
	<Section
		title='Radio'
		context='it a user select only one of a limited number of choices.'>
		<HoverCopy code={code.Radio}>
			<CodeReadOnly code={code.Radio} />
		</HoverCopy>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control" type="radio" name="r-ex" value="1" />radio 1</label>
			<label className="n-form-check"><input className="n-form-control" type="radio" name="r-ex" value="2" />radio 2</label>
			<label className="n-form-check"><input className="n-form-control" type="radio" name="r-ex" value="3" />radio 3</label>
		</div> 
	</Section>
);

export const InlineCheckboxes = () => (
	<Section
		title='Inline radio and checkboxes'
		context={<>instead using <b>.n-form-check</b> class. you must use <b>.n-form-check-inline</b> class otherwise.</>}>
		<HoverCopy code={code.InlineCheckboxes}>
			<CodeReadOnly code={code.InlineCheckboxes} />
		</HoverCopy>
		<div className="n-form-field">
			<label className="n-form-check-inline"><input className="n-form-control" type="checkbox" name="cb-ex" value="1" />checkbox 1</label>
			<label className="n-form-check-inline"><input className="n-form-control" type="checkbox" name="cb-ex" value="2" />checkbox 2</label>
			<label className="n-form-check-inline"><input className="n-form-control" type="checkbox" name="cb-ex" value="3"/>checkbox 3</label>
		</div>
		<div className="n-form-field n-form-inline">
			<label className="n-form-check-inline"><input className="n-form-control" type="radio" name="r-ex" value="1" />radio 1</label>
			<label className="n-form-check-inline"><input className="n-form-control" type="radio" name="r-ex" value="2" />radio 2</label>
			<label className="n-form-check-inline"><input className="n-form-control" type="radio" name="r-ex" value="3" />radio 3</label>
		</div> 
	</Section>
);

export const Switch = () => (
	<Section
		title='Switch'
		context='it a user select ZERO or MORE options of a limited number of choices.'>
		<HoverCopy code={code.Switch}>
			<CodeReadOnly code={code.Switch} />
		</HoverCopy>
		<div className="n-form-field">
			<div className="n-switch">
				<input id="switch-1" type="checkbox" />
				<label className="n-switch-label" htmlFor="switch-1">switch</label>
			</div> 
			<div className="n-switch">
				<input id="switch-2" type="checkbox" disabled />
				<label className="n-switch-label" htmlFor="switch-2">(disabled)</label>
			</div>
		</div> 
	</Section>
);

export const Range = () => (
	<Section
		title='Range'
		context='defines a control for entering a number whose exact value is not important (like a slider control).'>
		<HoverCopy code={code.Range}>
			<CodeReadOnly code={code.Range} />
		</HoverCopy> 
		<div className="n-form-field">
			<input className="n-form-control" type="range" name="range-ex"/>
		</div> 
	</Section>
);

export const Color = () => (
	<Section
		title='Color'
		context='a color picker can show up in the input field.'>
		<HoverCopy code={code.Color}>
			<CodeReadOnly code={code.Color} />
		</HoverCopy> 
		<div className="n-form-field">
			<input className="n-form-control" type="color" name="color-ex"/>
		</div> 
	</Section>
);

export const Textarea = () => (
	<Section
		title='Textarea'
		context='element defines a multi-line input field.'>
		<HoverCopy code={code.Textarea}>
			<CodeReadOnly code={code.Textarea} />
		</HoverCopy> 
		<textarea className="n-form-control" name="comment" placeholder="enter comment..." />
	</Section>
);

export const Select = () => (
	<Section
		title='Select'
		context='element defines a drop-down list.'>
		<HoverCopy code={code.Select}>
			<CodeReadOnly code={code.Select} />
		</HoverCopy> 
		<select className="n-form-control" name="selector">
			<option value="">choose wisely...</option>
			<option value="1">option 1</option>
			<option value="2">option 2</option>
		</select> 
	</Section>
);

export const ActiveState = () => (
	<Section
		title='Active state'
		context='.active class will forcely focus the form fields.'>
		<HoverCopy code={code.ActiveState}>
			<CodeReadOnly code={code.ActiveState} />
		</HoverCopy> 
		<div className="n-form-field">
			<input className="n-form-control active" type="text" placeholder="enter text..." name="text"/>
		</div>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control active" type="checkbox" name="cb" value="1" />checkbox</label>
		</div>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control active" type="radio" name="r" value="1" />radio</label>
		</div>
		<div className="n-form-field">
			<input className="n-form-control active" type="range" name="range" />
		</div>
		<div className="n-form-field">
			<input className="n-form-control active" type="color" name="color" />
		</div>
		<div className="n-form-field">
			<textarea className="n-form-control active" name="comment" placeholder="enter comment..." />
		</div>
		<div className="n-form-field">
			<select className="n-form-control active">
				<option value="">choose wisely...</option>
				<option value="1">option 1</option>
				<option value="2">option 2</option>
			</select>
		</div> 
	</Section>
);

export const DisabledState = () => (
	<Section
		title='Disabled state'
		context='Specifies that an form field should be disabled.'>
		<HoverCopy code={code.DisabledState}>
			<CodeReadOnly code={code.DisabledState} />
		</HoverCopy> 
		<div className="n-form-field">
			<input className="n-form-control" type="text" placeholder="enter text..." name="text" disabled />
		</div>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control" type="checkbox" name="cb" value="1" disabled />checkbox</label>
		</div>
		<div className="n-form-field">
			<label className="n-form-check"><input className="n-form-control" type="radio" name="r" value="1" disabled />radio</label>
		</div>
		<div className="n-form-field">
			<input className="n-form-control" type="range" name="range" disabled />
		</div>
		<div className="n-form-field">
			<input className="n-form-control" type="color" name="color" disabled />
		</div>
		<div className="n-form-field">
			<textarea className="n-form-control" name="comment" placeholder="enter comment..." disabled />
		</div>
		<div className="n-form-field">
			<select className="n-form-control" disabled>
				<option value="">choose wisely...</option>
				<option value="1">option 1</option>
				<option value="2">option 2</option>
			</select>
		</div> 
	</Section>
);